<template>
	<div class="wrap">
		<div class="register">
			<form>
				<h2>用户注册</h2>
				<p class="regist">
					<input class="username" type="text" placeholder="用户名" />
				</p>
				<p class="regist">
					<input class="password" type="password" placeholder="密码" />
				</p>
				<p class="regist">
					<input class="password" type="password" placeholder="确认密码" />
				</p>
        <div class="regist">
          <p class="left">兴趣爱好:</p>
          <div class='a' v-for='item,index in arr' :key='index'>
            <input type="checkbox" v-model='hobby' :value='item'>{{item}}
          </div>
        </div>
				<p class="everconfirmed">
					<input type="checkbox" v-model='off'/>条款以阅读
				</p>
				<p class="btn">
					<input type="button" disabled="disabled" value="注册" :class='{on: off}'/>
				</p>
				<p class="login">
					<span>
						<router-link to='/login'>已有账号，去登录</router-link>
					</span>
				</p>
			</form>
		</div>
	</div>
</template>

<script>
	export default {
		name: "register",
		data(){
			return {
        hobby: [],
        arr: [
          '打球',
          '睡觉',
          '听音乐',
          '看电影'
        ],
				off: false
			}
		}
	}
</script>

<style scoped lang="stylus">
  .a
    display: inline-block
	a
		text-decoration: none
		color: rgb(130,183,233);
	.wrap
		.register
			margin:0 (38/2)px
			text-align: center
			h2
				font-size: 18px
				margin-top: 20px
				margin-bottom: (135/2)px
			.regist
				width: 100%
				height: (78/2)px
				border: 1px solid #ededed
				margin-bottom: (60/2)px
				input
					width: 100%
					height: 100%
					background: #f3f3f3
					text-indent: (20/2)px
					border: none
			.everconfirmed
				width: 100%
				height: (32/2)px
				text-align: left
				font-size: (32/2)px
				font-weight: 700
				margin-bottom: (40/2)px
				input
					margin-right: (16/2)px
			.btn
				margin-bottom: (68/2)px
				input
					width: 100%
					height: (98/2)px
					border: 1px solid #acacac
					font-size: (42/2)px
					background: #999
					color: #fff
				.on
					background: red
					color: #fff
			.login
				color: #73aee6
</style>



